/* min width of the sidebar - it won't go smaller than this value */
export const SIDEBAR_MIN_WIDTH = 250;
/* max width of the sidebar - it won't go greater than this value */
export const SIDEBAR_MAX_WIDTH = 700;
/* threshold `width` for the sidebar on which it will collapse entirely */
export const SIDEBAR_THRESHOLD = 150;



